.frontWallContainer{
    /* purple */
    display:flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    gap:5%;
    width: 100%;
    height: 100%;
    background-color: black;
}

.frontWallContainer__leftUpCorner{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 30%;
    height: 30%;
    border-radius: 0% 0% 30% 0%;
}
.frontWallContainer__middleUp{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 30%;
    height: 30%;
    border-radius: 0% 0% 30% 30%;
}
.frontWallContainer__rightUpCorner{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 30%;
    height: 30%;
    border-radius: 0% 0% 0% 30%;
}



.frontWallContainer__leftCenterCorner{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 30%;
    height: 30%;
    border-radius: 0% 30% 30% 0%;
}
.frontWallContainer__middleCenterUp{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 30%;
    height: 30%;
    border-radius: 30% 30% 30% 30%;
}
.frontWallContainer__rightCenterorner{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 30%;
    height: 30%;
    border-radius: 30% 0% 0% 30%;
}



.frontWallContainer__leftDownCorner{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 30%;
    height: 30%;
    border-radius: 0% 30% 0% 0%;
}
.frontWallContainer__middleDown{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 30%;
    height: 30%;
    border-radius: 30% 30% 0% 0%;
}
.frontWallContainer__rightDownCorner{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 30%;
    height: 30%;
    border-radius: 30% 0% 0% 0%;
}

.red_O_front{
    background-color: red;
    box-shadow: 0 0 5vh red;
    color:white;
    text-shadow: 0 0 1vh white;
}
.redHover_front{
    background-color: red;
    box-shadow: 0 0 5vh red;
    transition: 0.2s ease-in-out;
}.redNotHover_front{
    background-color: rgb(36, 36, 36);
    transition: 1s ease-in-out;
}

.blue_X_front{
    background-color: blue;
    box-shadow: 0 0 5vh blue;
    color:white;
    text-shadow: 0 0 1vh white;
}
.blueHover_front{
    background-color: blue;
    box-shadow: 0 0 5vh blue;
    transition: 0.2s ease-in-out;
}
.blueNotHover_front{
    background-color: rgb(36, 36, 36);
    transition: 1s ease-in-out;
}

.clickNone_front{
    pointer-events: none;
}
